import { Tab } from '@headlessui/react';
import tour from '../img/tour.jpg';
import pulse from '../img/pulse.jpg';
import english from '../img/eng.jpg';
import windows from '../img/windows.jpg';
import notes from '../img/notes.jpg';
import weather from '../img/weather.jpg';

import { VscGithub } from "react-icons/vsc";
import { BsBrowserSafari } from "react-icons/bs";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

import GetJsonApi from './GetJsonApi';

import { useState, useEffect } from 'react';

const Works = () => {
    // Состояние для отслеживания наведения
    
    const [projects, setProjects] = useState([]);

    useEffect(() => {
        getService();
    }, [])

    const service = new GetJsonApi();
    
    const getService = async () => { // Создали получение данных через Api(тут он был на примере json файла)
        try {
            const data = await service.getApi(); // Предполагаем, что getApi() возвращает Promise
            console.log(data.data)
            setProjects(data.data); // Сохраняем данные в состояние
        } catch (error) {
            console.error('Ошибка при получении данных:', error);
        }
    };

    // Разделяем массив: первые 4 карточки для первого таба, остальные для второго
    const firstTabProjects = projects.slice(0, 4);
    const secondTabProjects = projects.slice(4);

    return (
        <>
            <section id='works' className="works">
                <div className="container py-11 mx-auto text-center w-4/5">
                    <h1 className="works-title xs:text-3xl sm:text-4xl md:text-4xl lg:text-5xl xl:text-4xl font-bold uppercase text-center ml-auto mr-auto">
                        Мои проекты
                    </h1>
                    <Tab.Group>
                        <Tab.List className='pt-10 flex lg:justify-center xs:block'>
                            <CenterMode/>
                        </Tab.List>
                        <Tab.Panels>
                            
                                <Tab.Panel className='flex-wrap flex md:justify-around pt-7 justify-around items-center'>      
                                    <RenderTabsContent arr={firstTabProjects}/>
                                </Tab.Panel>
                                <Tab.Panel className='flex-wrap flex md:justify-center pt-7 justify-around items-center gap-12'>
                                    <RenderTabsContent arr={secondTabProjects}/>
                                </Tab.Panel>
                            
                        </Tab.Panels>
                    </Tab.Group>
                </div>
            </section>
        </>
    );
};

export default Works;

function CenterMode() {
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);  
    
    // Отслеживание изменения ширины окна
    useEffect(() => {
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize); // Удаляем обработчик при размонтировании
    }, []);

    const settings = {
      className: "center",
      centerMode: false,
      infinite: false,
      centerPadding: "60px",
      slidesToShow: 1,
      speed: 500
    };

    // Функция для обновления состояния при изменении ширины окна
    const handleResize = () => {
        setIsMobile(window.innerWidth <= 768);
    };

    return (
        <>
          {isMobile ? (
            <Slider {...settings}>
              <Tab className='min-h-16 min-w-48 bg-black text-white p-2.5 uppercase font-bold rounded-lg'>
                Веб-Сайты
              </Tab>
              <Tab className='min-h-16 min-w-48 bg-black text-white p-2.5 md:ml-3.5 uppercase font-bold rounded-lg xs:ml-0 xs:mt-0'>
                Веб-приложения
              </Tab>
            </Slider>
          ) : (
            <>
              <Tab className='min-h-16 min-w-48 bg-black text-white p-2.5 uppercase font-bold rounded-lg'>
                Веб-Сайты
              </Tab>
              <Tab className='min-h-16 min-w-48 bg-black text-white p-2.5 md:ml-3.5 xs:ml-3.5 uppercase font-bold rounded-lg sm:ml-0 xs:ml-0'>
                Веб-приложения
              </Tab>
            </>
          )}
        </>
      );
  }

const RenderTabsContent = ({arr}) => {
    const [hoveredIndex, setHoveredIndex] = useState(null);
    return (
        <>
        {arr.map((item, index) => { // Если используем фигурные скобки то обязательно пишем return, если обычные то не нужно
        const imagePath = require(`../img/${item.image}`);

        return  <div key={index}
                onMouseEnter={() => setHoveredIndex(index)} // Устанавливаем индекс на 0 при наведении
                onMouseLeave={() => setHoveredIndex(null)} // Сбрасываем индекс при уходе курсора
                className={`works-wrap__block relative my-3.5 ${hoveredIndex === index ? '' : ''}`}
            >
                    <img src={imagePath} alt={item.altName} className="max-w-xs rounded-lg" />
    
                    <div
                    className={`works-wrap__block link absolute text-center w-full flex flex-col h-full bg-rgba(255,255,255,.75) bottom-0
                                transition-all duration-500 ease-in-out delay-150 backdrop-blur-md rounded-lg items-center justify-center 
                                ${hoveredIndex === index ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible translate-y-5'}`}
                    >
                        <h1 className="works-wrap__block link-text font-bold lg:text-2xl text-lg text-black">
                            {item.title}
                        </h1>
                        <div className="works-wrap_divlock link-title font-semibold text-black lg:text-xl leading-6">
                            {item.description}
                        </div>
                        <div className="w-1/2 h-0.5 bg-black mx-auto mt-8"></div>
    
                        <div className="works-wrap__block mt-4	 flex justify-center works-wrap">
    
                        <div className="works-wrap__block link-front flex items-center">
                            <VscGithub />
                            <a href={item.github} target='_blank' rel="noopener noreferrer">
                                <p className="works-wrap__block text ml-1 text-lg">GitHub</p>
                            </a>
                        </div>
                        <div className="works-wrap__block ml-2 link-front flex items-center">
                            <BsBrowserSafari />
                            <a href={item.website} target='_blank' rel="noopener noreferrer">
                                <p className="works-wrap__block text ml-1 text-lg">Сайт</p>
                            </a>
                        </div>
    
                    </div>
                </div>
            </div>
        })}
        </>
    )
}