import axios from 'axios';
import { Component } from "react";

class GetJsonApi extends Component {

    getApi = async () => { // Лучше использовать async/await нежели then и catch, они работают более не стабильно
        const jsonUrl = `${process.env.PUBLIC_URL}/data.json`;
        try {
            const response = await axios(jsonUrl);
            console.log(response)
            return response;
        } catch(error) {
            console.error('Ошибка при получении данных:', error);
            throw error; // Пробрасываем ошибку, чтобы обработать её в компоненте
        }
    }
}

export default GetJsonApi;